import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import {
  Button,
  Form,
  FormGroup, Input, Label
} from 'reactstrap'

export const NewOperatForm = (props) => {
  const user = useSelector(state => state.global.user.user)
  const [operat, setOperat] = useState({
    name: '',
    orderId: props.orderId,
    operatorId: user.id,
    operatTypeId: '',
    isTest: false
  })

  const handleSubmit = (e) => {
    e.preventDefault()
    const operatTypeParameters = props.operatTypes.find(x => x.id === operat.operatTypeId).parameters
    props.handleCreateOperat(operat, operatTypeParameters, true)
  }

  const handleOnChange = (e) => {
    const newValue = e.target.type === 'checkbox'
      ? e.target.checked
      : e.target.value
    const newOperat = {
      ...operat,
      [e.target.name]: newValue
    }
    setOperat(newOperat)
  }

  return (
    <Form
      id='new-operat-form' onSubmit={handleSubmit}
    >
      <Input
        type='select' required className='dark-select' id='operat-type-select' name='operatTypeId' value={operat.operatTypeId} onChange={handleOnChange} style={{ width: '100%' }} placeholder='Wybierz typ operatu'
      >
        <option value='' disabled>Wybierz rodzaj operatu</option>
        {props.operatTypes.map((element) => {
          return (
            <option key={element.id} value={element.id}>{element.name}</option>
          )
        })}
      </Input>
      <FormGroup>
        <Label for='operat-name'>
          Nazwa operatu
        </Label>
        <Input type='text' id='operat-name' name='name' required onChange={handleOnChange} />
      </FormGroup>
      <FormGroup>
        <Input
          type='checkbox'
          id='operat-is-test'
          name='isTest'
          style={{ margin: '3px' }}
          checked={operat.isTest}
          onChange={handleOnChange}
        />
        <Label for='operat-is-test'>
          Operat testowy
        </Label>
      </FormGroup>
      <Button type='submit' color='success'>Stwórz operat</Button>
    </Form>
  )
}

export default NewOperatForm
