import React, { useState, useEffect } from 'react'
import { operatService, clientFilesService } from 'services'
import {
  Button, Col, FormGroup, Input, Label, Collapse, Card, CardBody
} from 'reactstrap'
import { modalTypeConstants, userRolesConstants } from '../../constants'
import ReviewForm from 'components/ReviewForm'
import { useHistory } from 'react-router-dom'
import confirm from 'reactstrap-confirm'
import { useDispatch, useSelector } from 'react-redux'
import { showLoadingScreen, hideLoadingScreen } from 'views/Modal/actions'
import { useTranslation } from 'react-i18next'
import UserInfo from 'views/Common/UserInfo'
export const addReview = async (event, rating, order, operatId, dispatch) => {
  event.preventDefault()
  const comment = event.target.elements['review-comment'].value
  dispatch(showLoadingScreen(modalTypeConstants.LOADING, { message: 'addReview', translate: true }))
  const review = {
    comment,
    rating,
    operatorId: order.operators[0],
    clientId: order.clientId,
    operatId
  }
  operatService.postReview(review).catch(err => console.log(err)).finally(() => dispatch(hideLoadingScreen()))
}

function SignedOrder (props) {
  const user = useSelector(state => state.global.user)
  const isAdmin = user.userType === userRolesConstants.ADMIN
  const isOperator = user.userType === userRolesConstants.OPERATOR
  const isClient = user.userType === userRolesConstants.CLIENT
  const [operatorContactOpen, setOperatorContactOpen] = useState(false)
  const [userInfo, setUserInfo] = useState(false)
  const dispatch = useDispatch()
  const [operat, setOperat] = useState(null)
  const history = useHistory()
  const {t} = useTranslation('common')
  const downloadSignedOperat = async () => {
    dispatch(showLoadingScreen(modalTypeConstants.LOADING, { message: 'downloadOperat', translate: true, t:t }))
    clientFilesService.getFiles(props.activeOrder.clientId).then(response => {
      for (const file of response) {
        if (file.item2.includes(props.activeOrder.id + '.pdf')) {
          dispatch(hideLoadingScreen())
          window.open(
            file.item2, '_blank')
          return
        }
      }
      dispatch(hideLoadingScreen())
      dispatch(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'downloadOperatFailed', translate: true }))
    }).catch(err => {dispatch(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'downloadOperatFailed', translate: true }))})
  }
  useEffect(() => {
    operatService.getByOrderId(props.activeOrder.id).then((res) => {
      setOperat(res)
    })
  }, [props.activeOrder])
  return (
    <>
      <Button
        style={{ width: '272px', marginBottom: '1rem' }}
        color='primary'
        onClick={async () => await downloadSignedOperat()}
      >
        Pobierz operat
      </Button>
      <br />
      { 
      (isClient || isAdmin) &&
        <>
          <Button
            color='info' style={{ width: '272px', marginBottom: '1rem' }} onClick={() => {
              history.push({ pathname: '/users-profile', search: '?operatorId=' + props.activeOrder.operators[0] })
            }}
          >Dane o rzeczoznawcy
          </Button>
          <div>
            <Button
              style={{ width: '272px', marginBottom: '1rem' }}
              color='info'
              onClick={() => setOperatorContactOpen(!operatorContactOpen)}
            >
              Napisz do rzeczoznawcy
            </Button>
            <Collapse isOpen={operatorContactOpen} style={{ marginTop: '1rem' }}>
              <Card>
                <CardBody>
                  {operatorContactOpen && <UserInfo id={props.activeOrder.operators[0]} />}
                </CardBody>
              </Card>
            </Collapse> 
          </div>  
        </>
      }
      {
        (isOperator || isAdmin) &&
        <div>
          <Button
          style={{ width: '272px', marginBottom: '1rem' }}
          color='info'
          onClick={() => setUserInfo(!userInfo)}
        >
          informacje o zleceniodawcy
        </Button>
        <Collapse isOpen={userInfo} style={{ marginTop: '1rem' }}>
          <Card>
            <CardBody>
              {userInfo && <UserInfo id={props.activeOrder.clientId} />}
            </CardBody>
          </Card>
        </Collapse>
        </div>
      }
      <Col
        sm={{
          size: 4
        }}
      >
        { (isClient || isAdmin) &&
        <FormGroup>
          <Input
            id='checkbox2'
            type='checkbox'
            onChange={async () => {
              const result = operat.isObstructed
                ? true
                : await confirm({
                  title: <>Czy operat został zakwestionowany przez sąd?</>,
                  message: 'Zakwestionowane operaty są widoczne w statystyce rzeczoznawców i wpływają na ocenę koncową pracy rzeczoznawcy',
                  confirmText: 'Zakwestionuj',
                  cancelText: 'Anuluj',
                  confirmColor: 'success',
                  cancelColor: 'link text-primary'
                })
              if (result) {
                dispatch(showLoadingScreen(modalTypeConstants.LOADING, { message: 'sendData', translate: true }))
                operatService.obstructOperat(operat.id).then(() => {
                  setOperat({ ...operat, isObstructed: !operat.isObstructed })
                }).finally(() => dispatch(hideLoadingScreen()))
              }
            }}
            checked={operat && operat.isObstructed}
          />
          {' '}
          <Label>
            Operat zakwestionowany
          </Label>
        </FormGroup>
      }
      { (isOperator || isAdmin) && 
        <Label>
        Operat zakwestionowany: {operat && operat.isObstructed ? "Tak": "Nie"}
        </Label>
      }
      </Col>
      {operat && (isClient || isAdmin) && <ReviewForm operat={operat} onSubmit={(e, rating) => addReview(e, rating, props.activeOrder, operat.id, dispatch)} />}
    </>
  )
}

export default SignedOrder
