import React, { useState, useRef } from 'react'
import NotificationAlert from 'react-notification-alert'
import { useHistory } from 'react-router-dom'
import { modalTypeConstants, globalConstants } from '../../constants'
import UserInfo from 'views/Common/UserInfo'
import {
  Button,
  Card,
  CardBody,
  Collapse,
  Input
} from 'reactstrap'
import { useDispatch } from 'react-redux'
import { showLoadingScreen } from 'views/Modal/actions'
import { createOperat } from 'views/History/actions'

function PaidOrder (props) {
  const dispatch = useDispatch()
  const [operatType, setOperatType] = useState({})
  const [userInfo, setUserInfo] = useState(false)
  const history = useHistory()
  const notifyRegister = useRef(null)

  const handleCreateOperat = (row) => {

    const timeElapsed = Date.now()
    const today = new Date(timeElapsed)
    const operat = {
      name: `${row.name + ', ' + operatType.name + ' : ' + today.toLocaleString()}`,
      orderId: row.id,
      operatorId: row.operators[0],
      operatTypeId: operatType.id,
      isTest: false
    }
    
    if(operat.orderId === globalConstants.EMPTY_GUID || operat.operatorId === globalConstants.EMPTY_GUID) {
      dispatch(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'createOperatFailed', translate: true }))
      return
    }
    dispatch(createOperat(operat, operatType.parameters, false))
    history.push('/history')
  }


  const dropDown = () => {
    return (
      <div>
        <Input
          type='select' className='dark-select' id='operat-type-select' 
          value={operatType.id ? operatType.id : ''} 
          onChange={(e) => {setOperatType(props.operatTypes.find(x => x.id === e.target.value))}} 
          style={{ width: '272px' }} placeholder='wybierz typ operatu'
        >
          <option value='' disabled>Wybierz rodzaj operatu</option>
          {props.operatTypes.map((element) => {
            return (
              <option key={element.id} value={element.id}>{element.name}</option>
            )
          })}
        </Input>
      </div>
    )
  }

  return (
    <div>
      <NotificationAlert ref={notifyRegister} />
      <div>
          {dropDown()}
          <div className='d-flex justify-content-between'>
            <Button
              style={{ width: '272px', marginBottom: '1rem' }}
              color='primary'
              onClick={() => handleCreateOperat(props.activeOrder)}
              disabled={operatType === {}}
            >
              Stwórz operat
            </Button>
          </div>
        <Button
          style={{ width: '272px', marginBottom: '1rem' }}
          color='info'
          onClick={() => setUserInfo(!userInfo)}
        >
          informacje o zleceniodawcy
        </Button>
        <Collapse isOpen={userInfo} style={{ marginTop: '1rem' }}>
          <Card>
            <CardBody>
              {userInfo && <UserInfo id={props.activeOrder.clientId} />}
            </CardBody>
          </Card>
        </Collapse>
      </div>
    </div>
  )
}

export default PaidOrder
